body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;
  padding: 32px 0 16px;
}

.card__image {
  margin-bottom: 40px;
  padding-inline: 19px;
  display: flex;
}

.card__image img {
  width: 160px;
  height: 134px;
}

.card__content {
  padding-inline: 16px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  margin-bottom: 24px;
}

.card__price {
  margin-bottom: 16px;
}

.card__link {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__link[data-qa="hover"]:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.rating {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.rating__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__money {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star--1, .stars__star--2, .stars__star--3, .stars__star--4 {
  background-image: url("star-active.edda4c43.svg");
}

